// AnimatedBackground.jsx

import React from 'react';
import styles from '../Style/AnimatedBackground.module.css';
import img1 from '../img/slide1.jpg'


const AnimatedBackground = ({ speed }) => {
    const images = [img1, img1, img1,img1,img1,img1,img1, img1,img1,img1,img1,img1
    , img1,img1,img1,img1,img1, img1,img1,img1,img1,img1, img1,img1,img1,img1,img1]; // 이미지 배열
  
    const animationDuration = `${speed}s`;
  
    return (
      <div
        className={styles.animatedBackground}
        style={{ animationDuration }}
      >
        {images.map((image, index) => (
          <div
            key={index}
            className={styles.slide}
          >
            <img src={image} alt={`Slide ${index}`} />
          </div>
        ))}
      </div>
    );
  };
  
  export default AnimatedBackground;